.input-group {
  position: relative;
}

.input-group input {
  /* background: url("../../images/search-icon.png") 20px center right no-repeat; */
}

.input-group #errorMessage {
  color: red;
}

#resultsContainer {
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  margin: 30px 0;
}

#resultsContainer .result-column {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0 3px rgba(black, 0.2);
  padding: 25px 15px;
  word-break: break-word;
}

@media (min-width: 992px) {
  #resultsContainer .result-column {
    width: calc(50% - 10px);
    max-height: 400px;
    overflow-y: scroll;
    margin-bottom: 30px;
  }
}

.youtube-column .result-card {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid #767676;
  padding-bottom: 10px;
  margin-bottom: 30px;
}

.youtube-column>a {
  padding-right: 15px;
}
